* {
  box-sizing: border-box;
}
.directory-list {
  position: fixed;
  top: 88px;
  padding-left: 10px;
  font-size: 12px;
  list-style: none;
}
.directory-list .tree-num {
  line-height: 24px;
  border-left: 2px solid #e8e8e8;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  cursor: pointer;
}
.directory-list .directory-item-active {
  border-left: 2px solid #25b864;
  color: #25b864;
  box-sizing: content-box;
  box-sizing: initial;
}
.directory-list p {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
